@use 'src/styles/consts';
@use 'src/styles/templates';

.commentItem {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 10px;
}

.date {
  text-align: left;
  font-size: consts.$font-size-xs;
  margin: 0px;
  -webkit-box-flex: 0;
  flex-grow: 0;
  color: var(--secondary-color);
  white-space: nowrap;
  overflow: hidden;
}

.author {
  font-size: consts.$font-size-m;
  font-weight: consts.$font-weight-bold;
  color: consts.$color-blue-title;
}

.mainText {
  overflow-wrap: anywhere;
  font-size: 14px;
}

.addToBlockForm {
  @extend %addToBlockForm;
}

.inputWithTooltip {
  @extend %inputWithTooltip;
}

.addToBlockInput {
  @extend %addToBlockInput;
  resize: vertical;
}
