@use 'src/styles/consts';
@use 'src/styles/templates';

.datesFilterButton {
  @extend %customButton;
  position: fixed;
  bottom: 10px;
  text-align: left;
  left: 10px;
  z-index: consts.$z-index-l;
  color: var(--secondary-color);
  font-weight: consts.$font-weight-bold;
  background-color: var(--primary-color);
  border: 1px solid var(--secondary-color);
  opacity: 0.5;
  transition: 300ms;


  &:hover {
    transform: none;
    white-space: nowrap;
    width: 150px;
    opacity: 1;
  }

  &Text_visible {
    display: flex;
    justify-content: left;
    align-items: center;
    padding-left: 33px;
    font-size: consts.$font-size-xs;
    opacity: 1;
    transition: opacity 225ms linear;
    color: var(--secondary-color);
    cursor: pointer;
    animation: text-show-up 300ms;

    @keyframes text-show-up {
      0%, 80% {
        opacity: 0;
        visibility: hidden
      }
      100% {
        opacity: 1;
        visibility: visible
      }
    }
  }

  &Text_invisible {
    opacity: 0;
    width: 0;
  }
}


.image {
  width: 75%;
  height: 75%;
  filter: var(--svg-icon-color);

  &Container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: -1px;
    top: -1px;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: 3px solid var(--secondary-color);
    border-radius: 50%;
    width: 35px;
    height: 35px;
  }
}

