@use 'src/styles/consts';
@use 'src/styles/templates';

.personalizationPanelWrapper {
  @extend %FormWrapper;
  @media (max-width: consts.$breakpoint-mobile) {
    min-width: 300px;
  }
  background-color: var(--primary-color);
  transition: 300ms;
}

.cancelBtn {
  @extend %closeDeleteFormElementBtn;
}

.submitFilteringBtn {
  @extend %newElementInFormBtn;
  background-color: var(--secondary-color);
  border: 3px solid var(--primary-color);
  color: var(--primary-color);
}

.logOutContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logOutButton {
  @extend %newElementInFormBtn;
  background-color: var(--secondary-color);
  border: 3px solid var(--primary-color);
  color: var(--primary-color);
  width: 180px;
}

.resetFilteringBtn {
  @extend %newElementInFormBtn;
  background-color: var(--primary-color);
  border: 3px solid var(--box-shadow);
  color: var(--secondary-color);
}

.buttonsWrapper {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
}

.formTitle {
  @extend %newElementFormPropertyTitle;
}
