@use 'src/styles/consts';
@use 'src/styles/templates';

.container {
  position: relative;

  &:hover {
    transform: scale(1.01);
    transition: all 200ms;
  }

  a {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: none;
    box-shadow: none;
    border-radius: 10px;

    &:hover {
      background-color: var(--primary-color);
    }
  }
}

.addToBlockInput {
  @extend %addToBlockInput;
}

.addToBlockForm {
  @extend %addToBlockForm;
}

.inputWithTooltip {
  @extend %inputWithTooltip;
}
