@use 'src/styles/consts';
@use 'src/styles/templates';

.btn_active {
  background-color: consts.$color-dark;
  position: fixed;
  cursor: pointer;
  z-index: consts.$z-index-l;
  top: calc(#{consts.$appHeaderHeight} - 15px);
  left: calc(#{consts.$drawerWidth} - 10%);
}

.btn_inactive {
  @extend .btn_active;
  pointer-events: none;
  background-color: consts.$color-darkgrey;
}
// need !important to overwrite lib styles
.toolbar {
  background: var(--primary-color) !important;
  color: consts.$color-black !important;
  border: 1px solid var(--box-shadow) !important;
  border-radius: 10px !important ;
}

.editor {
  padding: 6px;
  -webkit-line-clamp: 2;
  border: 1px solid var(--box-shadow);
  border-radius: 10px;
}

.wrapper {
  border-radius: 7px;
  box-shadow: consts.$color-box-shadow-black-low-opacity;
}

:global(.rdw-list-wrapper) {
  z-index: consts.$z-index-s;
}

:global(.public-DraftStyleDefault-block) {
  max-height: 100px;
  margin: 0 !important; // need !important to overwrite lib styles
}

:global(.DraftEditor-editorContainer) {
  z-index: consts.$z-index-xs;
}

:global(.rdw-list-dropdown) {
  z-index: consts.$z-index-xs;
}


.cards {
  &Form {
    width: 90%;
  }

  &Portal {
    margin: 80px 0;
  }
}

.codeReviewBlock {
  width: 50%;
  display: flex;
}

.codeStatus {
  width: 125px;
  display: flex;
  align-items: center;
  font-size: consts.$font-size-m;
  font-weight: consts.$font-weight-semi-bold;
  color: consts.$color-darkgrey;

  &:before {
    content: ' \2714';
    color: consts.$color-darkgreen;
    font-size: consts.$font-size-l;
    padding-right: 5px;
  }
}

.newElementFormPropertyInput {
  @extend %formInput;
  width: 100%;
  margin: 4px 0;
  padding: 4px;
}

.codeReviewBtn {
  min-width: 125px;
  min-height: 45px;
}
