@use 'src/styles/templates';
@use 'src/styles/consts';

@keyframes show-up {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.mainContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  border-radius: 50px;
  user-select: none;

  &:hover .confirmText {
    padding-left: 15px;
    animation: text-show-up forwards 700ms, show-up forwards 700ms;
    transition: 400ms;
    @keyframes text-show-up {
      0% {
        max-width: 0;
      }
      100% {
        max-width: 300px;
      }
    }
  }
}

.container {
  position: relative;
  height: 35px;
  transition: 400ms;
  border-radius: 50px;
  background-color: var(--primary-color);
  border: 3px solid var(--box-shadow);
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0 15px;

  &AtHeader {
    background-color: consts.$color-dark;
    border: 3px solid consts.$color-white;
  }
}

.icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 5px;
  left: 5px;
  flex: 1;
}

.confirmText {
  display: flex;
  justify-content: center;
  align-items: center;
  top: -1px;
  position: relative;
  max-width: 0;
  transition: 300ms;
  font-size: consts.$font-size-m;
  font-weight: consts.$font-weight-semi-bold;
  color: var(--secondary-color);

  &AtHeader {
    color: consts.$color-white;
  }
}

.confirmButtonsContainer {
  gap: 3px;
  display: flex;
  flex-direction: row;
  animation: show-up 400ms forwards;
}

.hide {
  animation: hide 400ms forwards;
  opacity: 1;
  @keyframes hide {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
}

.confirmButtons {
  @extend %customButton;
  margin: 0;
  font-size: consts.$font-size-m;
  border-color: var(--box-shadow);
  background-color: var(--primary-color);

  &AtHeader {
    background-color: consts.$color-dark;
    border: 3px solid consts.$color-white;
  }
}

.confirmButton {
  @extend .confirmButtons;
  color: consts.$color-green;

  &AtHeader {
    @extend .confirmButtonsAtHeader;
  }
}

.declineButton {
  @extend .confirmButtons;
  color: consts.$color-red;

  &AtHeader {
    @extend .confirmButtonsAtHeader;
  }
}
