@use 'src/styles/consts';

.tooltip {
  position: relative;
}

.tooltipText {
  opacity: 0;
  position: absolute;
  padding: 0 4px;
  top: 120%;
  left: 50%;
  width: max-content;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  border-radius: 2px;
}

.tooltip:hover .tooltipText {
  opacity: 1;
}
