@use 'src/styles/consts';
@use 'src/styles/templates';

.cardList {
  &Container {
    position: relative;
    flex: 1;
    transition: consts.$sideBarAnimationTime cubic-bezier(0, 0, 0.2, 1) 0ms;
    overflow-x: auto;
    height: 100%;

    &IsShrink {
      @extend .cardListContainer;
      flex: calc(1 - #{consts.$drawerWidthFlex});
    }

    &IsHidden {
      @extend .cardListContainer;
      flex: 0;
    }
  }

  @extend %scrollShowingOnHover;
  padding: 40px 0;
  overflow-x: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 20px;
  overflow-y: hidden;
  transition: consts.$sideBarAnimationTime cubic-bezier(0, 0, 0.2, 1) 0ms;
}
