@use "src/styles/consts";
@use "src/styles/templates";
.appBar {
  @extend %scrollShowingOnHover;
  overflow-x: hidden;
  display: flex;
  justify-content: flex-end;
  z-index: consts.$z-index-s;
  flex: 0;
  height: 100%;
  animation: hideContainer 300ms forwards;
  transition: consts.$sideBarAnimationTime cubic-bezier(0, 0, 0.2, 1) 0ms;

  @keyframes hideContainer {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  &Shift {
    @extend .appBar;
    flex: consts.$drawerWidthFlex;
    animation: showContainer consts.$sideBarAnimationTime forwards;

    @keyframes showContainer {
      0%, 50% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
  }

  &FullScreen {
    @extend .appBar;
    flex: 1;
  }
}


