@use 'src/styles/consts';

.searchWrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: ease all 0.5s;
  cursor: pointer;
  margin-right: 15px;

  @media (max-width: consts.$breakpoint-tablet) {
    width: 40%;
  }
}

.searchField {
  height: 35px;
  width: 35px;
  transition: ease all 0.5s;
  color: var(--secondary-color);
  font-size: consts.$font-size-m;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: relative;
  background-color: var(--primary-color);
  padding-left: 27px;
  border-radius: 50px;
  border: 3px solid consts.$color-white;
}

.searchField_opened {
  width: 250px;
  border-color: consts.$color-blue-focus;
  outline: 0;
  box-shadow: 0 0 0 2px consts.$color-blue-focus-box-shadow;
}

.searchIcon {
  height: 15px;
  width: 15px;
  position: absolute;
  left: 30px;
  z-index: consts.$z-index-l;
  filter: var(--svg-icon-color);
}
