@use "src/styles/consts";

.tooltip {
  width: fit-content;
  height: auto;
  color: consts.$color-black;
  border: .7px consts.$color-darkred solid;
  border-radius: 6px;
  padding: 5px;
  font-size: consts.$font-size-xs;
  text-align: left;
  position: absolute;
  z-index: consts.$z-index-l;
  top: 95%;
  background-color: consts.$color-white;
  white-space: nowrap;
  animation: wave 266ms ease;

  @keyframes wave {
    0% {
      opacity: 0;
    }
    100% {
      opacity: .8;
    }
  }

  &:after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 10%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent consts.$color-darkred transparent;
    z-index: consts.$z-index-m;
  }

  @media (max-width: 970px) {
    width: 180px;
  }

  &Image {
    width: 16px;
    height: 16px;
    display: inline
  }
}