@use 'src/styles/consts';
@use 'src/styles/templates';

.listItem {
  position: relative;
  width: calc(50% - 10px);
  display: flex;
  align-items: center;
  padding: 10px 15px;
  color: consts.$color-darkgrey;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 10px;
  border: none;
  box-shadow: var(--secondary-color) 0 4px 10px 0;

  &:hover {
    transform: scale(1.01);
    transition: all 200ms;
    background-color: var(--primary-color);
  }
}

.groupWrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
  gap: 10px;
  cursor: pointer;

  > .listItem:only-child {
    width: 100%;
  }
}

.deleteButtons {
  @extend %customButton;
  position: absolute;
  top: 0;
  z-index: 1;
  line-height: 15px;
  max-width: 25px;
  max-height: 25px;
  font-size: consts.$font-size-xs;
  font-weight: bold;
  &:hover {
    transform: scale(1.15);
  }
}

.itemDeleteButton {
  @extend .deleteButtons;
  right: 0;
}

.deleteConfirmationBtn {
  @extend .deleteButtons;
  right: 30px;
  color: consts.$color-green;
}
