@use "src/styles/consts";

.input {

  &Field {
    border: 0;
    border-bottom: 0.7px solid var(--box-shadow);
    width: 100%;
    font-size: consts.$font-size-m;
    line-height: 17px;
    transition: all .3s;
    background-color: var(--primary-color);
    color: var(--secondary-color);

    &:focus {
      outline: none;
      margin: 0;
      letter-spacing: inherit;
      line-height: 17px;
      box-sizing: border-box;
      box-shadow:0 5px 10px -8px var(--box-shadow);
    }
  }

  &Wrapper {
    position: relative;
    margin-bottom: 40px;
  }

  &Label {
    font-weight: consts.$font-weight-bold;
    font-size: consts.$font-size-m;
  }
}
