@use 'src/styles/consts';

.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primary-color);
}

.wrapper {
  width: 320px;
}

.form {
  height: 160px;
  width: 300px;
  background-color: var(--secondary-color);
  position: relative;
  border-radius: 8px;

  &:before {
    display: block;
    position: relative;
    height: 2px;
    background: var(--primary-color);
    content: '';
    top: 50%;
    margin-left: 20px;
    z-index: 1;
  }

  & input:first-of-type {
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
  }

  & input:last-of-type {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
  }

  & input[type='text'],
  & input[type='password'],
  & button [type='submit'] {
    background: var(--secondary-color);
    box-shadow: inset -100px -100px var(--secondary-color);
    color: var(--primary-color);
  }
}

.input {
  position: relative;
  display: block;
  width: 280px;
  height: 50%;
  border: 0;
  outline: 0;
  top: -2px;
  padding: 0 0 0 20px;
  font-weight: consts.$font-weight-bold;

  &::placeholder {
    color: var(--input-placeholder-color);
  }
  &:-webkit-autofill {
    -webkit-text-fill-color: var(--input-placeholder-color);
  }
}

.submitButton {
  cursor: pointer;
  display: block;
  position: absolute;
  width: 52px;
  height: 52px;
  background: var(--secondary-color);
  border-radius: 50px;
  outline: 0;
  z-index: 2;
  top: calc(50% - 52px / 2);
  right: -26px;
  border: 6px solid var(--primary-color);
  font-size: consts.$font-size-xl;
  text-indent: 0;
  padding-left: 9px;
  padding-bottom: 3px;
  text-align: center;
  transition: 300ms;

  &Disabled {
    @extend .submitButton;
    transition: 600ms;
    color: var(--input-placeholder-color);

    &:hover {
      transform: rotate(-180deg);
    }
  }

  &Active {
    @extend .submitButton;

    color: var(--primary-color);
    animation: pop-up 400ms;

    &:hover {
      transform: rotate(-90deg);
      transition: 300ms;
    }

    @keyframes pop-up {
      50% {
        width: 62px;
        height: 62px;
        right: -calc((62px / 2));
        top: calc(50% - 62px / 2);
      }
      100% {
        width: 52px;
        height: 52px;
        right: -26px;
      }
    }
  }
}
