@use 'src/styles/consts';
@use 'src/styles/templates';

.date {
  input {
    margin: 0;
  }

  &Block {
    margin: 10px 0;
    padding: 0;
    display: flex;
    justify-content: space-between;

    @media (max-width: 970px) {
      display: block;
    }
  }

  &Title {
    font-weight: consts.$font-weight-semi-bold;
    font-size: consts.$font-size-m;
    padding-bottom: 5px;
    display: flex;
    word-wrap: break-word;
    word-break: break-word;
  }

  &Picker {
    @extend %customDateTimePicker;
    border-radius: 10px;
  }

  input {
    padding-bottom: 11px;
  }

  &Time {
    @extend %customDateTimePicker;
    border-radius: 10px;
  }
}
