@use 'src/styles/consts';

//rd-list in Editor has z-index 90, our must be more
.tagsFormSelect {
  z-index: 99;
  font: inherit;
  text-overflow: ellipsis;
  width: 100%;
  border: 1px solid var(--box-shadow);
  cursor: pointer;
  box-shadow: none;
  outline: none;
  padding: 5px;
  transition: border 0.3s, box-shadow 0.3s;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 10px;
}

.tagsTitle {
  padding-bottom: 5px;
  display: flex;
  word-wrap: break-word;
  word-break: break-word;
  font-weight: consts.$font-weight-semi-bold;
  font-size: consts.$font-size-m;
}
