@use 'src/styles/consts';
@use 'src/styles/templates';

.cancelBtn {
  @extend %closeDeleteFormElementBtn;
}

.submitFilteringBtn {
  @extend %newElementInFormBtn;
  margin: 0 auto;
}

.resetFilteringBtn {
  @extend %newElementInFormBtn;
  margin: 0 auto;
  background-color: transparent;
  color: consts.$color-dark;
  border: 2px solid consts.$color-lightgrey;
}

.buttonsWrapper {
  display: flex;
  justify-content: center;
}

.dateFilterWrapper {
  @extend %FormWrapper;
  @media (max-width: consts.$breakpoint-mobile) {
    min-width: 300px;
  }
}

.formTitle {
  @extend %newElementFormPropertyTitle;
}

.inputWithTooltip {
  position: relative;
  div {
    right: 0px;
  }
}

.datePicker {
  @extend %customDateTimePicker;
  border-radius: 10px;
}

.dateTitle {
  padding-bottom: 5px;
  word-wrap: break-word;
  word-break: break-word;
}

.propertyWrapper {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}
