@use 'src/styles/consts';

.drawerContainer {
  border-left: 1px solid var(--box-shadow);
  background-color: var(--primary-color);
  color: var(--font-color);
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  height: 100%;
  position: relative;
  outline: 0;
  z-index: consts.$z-index-s;
  transition: consts.$sideBarAnimationTime cubic-bezier(0, 0, 0.2, 1) 0ms;
  overflow-y: auto;
}

.drawerHeader {
  padding: 20px 8px;
  // necessary for content to be below app bar
  //...theme.mixins.toolbar;

}

.sidebar {
  transition: consts.$sideBarAnimationTime cubic-bezier(0, 0, 0.2, 1) 0ms;
  height: 100%;
  max-width: 100%;

  &IsOpened {
    @extend .sidebar;
    flex: 1;
  }

  &IsClosed {
    width: 0;
    @extend .sidebar;
    flex: 0;
  }
}
