@use 'src/styles/consts';
@use 'src/styles/templates';

.video {
  &Wrapper {
    margin: 0 auto 120px;
    width: 100%;
  }

  &Resource {
    margin-left: 10px;
  }

  &Toggle {
    width: 90%;
    display: flex;
    justify-content: flex-end;
  }

  &CollapseBtn {
    min-width: 10%;
  }

  &Header {
    min-width: 90%;
    font: inherit;
    padding-left: 40px;
  }

  &Area_opened {
    overflow: hidden;
    padding-bottom: 42.55%;
    height: 0;
    display: block;
    position: relative;
    width: calc(80% - 35px);

    iframe {
      border: 0;
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      border-radius: 3px;
    }
  }

  &Area_closed {
    @extend .videoArea_closed;
    display: none;
  }

  &CollapsingLine_opened {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 35px;
    height: auto;
    cursor: pointer;
    color: consts.$color-dark;
    background-color: consts.$color-lightgrey;
    border-radius: 50px;
    transition: 0.4s;
    transform-origin: 100% 0;

    &:hover {
      opacity: 0.8;
    }
  }

  &CollapsingLine_closed {
    @extend .videoCollapsingLine_opened;
    width: 80%;
    height: 35px;
  }

  &Block_closed {
    position: relative;
    width: 100%;
    margin-bottom: 2px;
    display: block;
  }

  &Block_opened {
    @extend .videoBlock_closed;
    display: flex;
  }
}

.btn {
  &Expanded {
    @extend %customButton;
    position: absolute;
    left: 0;
    top: 0;
    background-color: consts.$color-white;
    transition: all 0.35s cubic-bezier(0.08, 1.09, 0.32, 1.275);
    width: 25px;
    height: 25px;
    margin-top: 5px;
  }

  &Collapsed {
    @extend .btnExpanded;
    transform: rotate(180deg);
  }

  &Deletion {
    width: 25px;
    height: 25px;
    margin: 5px;
  }

  &Confirmation {
    @extend %customButton;
    width: 25px;
    height: 25px;
    margin: 5px;
    color: consts.$color-green;
    animation: wave 0.3s ease-out;
    @keyframes wave {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }
  }
}

.delete {
  &Area_narrowed {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 25px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    cursor: pointer;
  }

  &Area_expanded {
    @extend .deleteArea_narrowed;
    height: 60px;
  }
}

.addToBlockForm {
  @extend %addToBlockForm;
}

.inputWithTooltip {
  @extend %inputWithTooltip;
}

.addToBlockInput {
  @extend %addToBlockInput;
}
