@use 'src/styles/consts';
@use 'src/styles/templates';

.alert {
  &Container {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    color: var(--primary-color);
    top: 20px;
    right: 0px;
    max-width: 150px;
    overflow: auto;
    background: var(--secondary-color);
    padding: 10px 10px;
    animation: show-up 1000ms forwards, hide 1000ms 3000ms forwards;

    @keyframes show-up {
      0% {
        opacity: 0;
        right: -150px;
      }
      100% {
        opacity: 1;
        right: 20px;
      }
    }
    @keyframes hide {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  }

  &Message {
    color: var(--primary-color);
    max-width: 100%;
    word-wrap: anywhere;
  }
}