@use "src/styles/consts";

.header {
  position: sticky;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: consts.$appHeaderHeight;
  width: 100%;
  padding: 0 20px;
  background-color: var(--secondary-color);
  font-size: consts.$font-size-xl;
  color: consts.$color-blue-focus;
  z-index: consts.$z-index-m;
  transition: all 0.3s ease-out;
}


