.switcher {
  margin-left: 40px;

  label {
    display: inline-block;
    position: relative;
    width: 60px;
    height: 34px;
  }

  input {
    width: 0;
    height: 0;
    opacity: 0;
  }

  span {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--primary-color);
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 34px;
    cursor: pointer;
    box-shadow: 0 0 3px var(--box-shadow);

    &:before {
      content: '';
      position: absolute;
      height: 26px;
      width: 26px;
      left: 4px;
      bottom: 4px;
      background-color: var(--secondary-color);
      -webkit-transition: 0.4s;
      transition: 0.4s;
      border-radius: 50%;
    }
  }

  input:checked + span {
    background-color: var(--primary-color);
  }

  input:checked + span:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }

  &Icon_left {
    display: inline;
    position: absolute;
    top: 2px;
    left: 63px;
    color: var(--secondary-color);
  }

  &Icon_right {
    @extend .switcherIcon_left;
    left: unset;
    right: 63px;
  }
}
