@use 'src/styles/templates';
@use 'src/styles/consts';

.btn_add {
  @extend %customButton;
  position: absolute;
  left: 80%;
  top: 15px;
}
.groupWrapper {
  display: flex;
  align-items: center;
  padding: 15px 0;
  gap: 20px;
  box-sizing: border-box;
}

.groupTitle {
  font-weight: consts.$font-weight-semi-bold;
  font-size: consts.$font-size-m;
}

.actionButton {
  border-radius: 50px;
  width: 35px;
  height: 35px;
  cursor: pointer;
  color: white;
  background-color: #282c34;
  border: 3px solid white;
  padding: 2px;
  transition: ease all 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px 0 5px;
  user-select: none;
}
