:root {
  --background-dark: white;
  --background-light: #282c34;
}

.light {
  --primary-color: white;
  --secondary-color: #282c34;
  --font-color: black;
  --box-shadow: rgba(0, 0, 0, 0.16);
  --input-placeholder-color: white;
  --svg-icon-color: invert(13%) sepia(17%) saturate(580%) hue-rotate(181deg) brightness(99%)
    contrast(90%);
}

.dark {
  --primary-color: #282c34;
  --secondary-color: #dadce0;
  --font-color: white;
  --input-placeholder-color: black;
  --box-shadow: rgba(255, 255, 255, 0.16);
  --svg-icon-color: invert(99%) sepia(88%) saturate(2%) hue-rotate(168deg) brightness(111%)
    contrast(100%);
}
