@use 'src/styles/consts';

.btn {
  width: 1%;

  &TogglingButtonArea {
    flex: 1 1 0;
    position: relative;

    @media (max-width: consts.$breakpoint-mobile) {
      width: 32%;
    }
    @media (max-width: consts.$breakpoint-tablet) {
      display: flex;
      flex-direction: row-reverse;
      width: 40%;
    }
  }

  &NewCard {
    position: absolute;
    font-size: consts.$font-size-xs;
    border-radius: 50px;
    height: 35px;
    border: 1px solid var(--primary-color);
    color: black;
    transition: all 300ms ease-in-out;
    right: 0;
    top: -17.5px;
    width: 35px;

    &Expanded{
      width: 100px;
    }

    &Transformed{
    transform: translateY(30px) rotate(45deg);
    }

    &PointerEvents{
      pointer-events: auto;
    }
  }

  &CardCircle {
    position: absolute;
    right: -1px;
    top: -1px;
    text-align: center;
    width: 35px;
    height: 35px;
    font-size: consts.$font-size-xl;
    line-height: 24px;
    border-radius: 50px;
    z-index: consts.$z-index-s;
    cursor: pointer;
    color: consts.$color-white;
    background-color: consts.$color-dark;
    border: 3px solid consts.$color-white;
  }

  &TextArea_visible {
    font-size: 10px;
    opacity: 1;
    position: absolute;
    font-weight: consts.$font-weight-bold;
    right: 27%;
    top: 28%;
    width: 60px;
    transition: opacity 225ms linear;
    color: var(--primary-color);
    cursor: pointer;
    animation: text-show-up consts.$transition-time;

    @keyframes text-show-up {
      0%, 80% {
        visibility: hidden
      }
      100% {
        visibility: visible
      }
    }
  }

  &TextArea_invisible {
    opacity: 0;
  }
}

.box {
  background-color: consts.$color-purpleblue ;
}




