@use 'src/styles/consts';

%newElementFormPropertyTitle {
  margin: 15px 0;
  font-size: consts.$font-size-m;
  font-weight: consts.$font-weight-bold;
  word-break: break-word;
  overflow: hidden;
  outline: none;
}

%saveExitButtons {
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid consts.$color-white;
}

%formInput {
  box-sizing: border-box;
  border: none;
  border-bottom: 0.1px solid consts.$color-darkgrey;
  outline: none;
}

%linkInput {
  @extend %formInput;
  width: 80%;
  padding: 5px;
  font: inherit;
  text-overflow: ellipsis;
  transition: all 0.4s;
}

%FormWrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid consts.$color-box-shadow-black-low-opacity;
  border-radius: 7px;
  background-color: consts.$color-white;
  padding: 10px 20px;
  position: relative;
  min-width: 400px;
}

%customButton {
  border-radius: 50px;
  width: 35px;
  height: 35px;
  cursor: pointer;
  color: consts.$color-white;
  background-color: consts.$color-dark;
  border: 3px solid consts.$color-white;
  padding: 2px;
  transition: ease all 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px 0 5px;

  &Indicator {
    color: var(--secondary-color);
    position: relative;
    top: -23px;
    left: 10px;
    font-size: consts.$font-size-m;
    border: 3px solid var(--secondary-color);
    border-radius: 6px;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 1px;
  }

  &:hover {
    transform: scale(1.05);
  }

  & img {
    max-width: 100%;
    padding: 1px;
  }

  &[hidden] {
    display: none;
  }
}

%newElementInFormBtn {
  @extend %customButton;
  min-width: 125px;
  min-height: 45px;
}

%closeDeleteFormElementBtn {
  @extend %customButton;
  position: absolute;
  top: -15px;
  right: -15px;
}

%scrollShowingOnHover {
  &::-webkit-scrollbar {
    width: 10px;
    background-color: var(--primary-color);
  }

  &::-webkit-scrollbar-thumb {
    background-color: transparent;
  }
  &:hover {
    &::-webkit-scrollbar-thumb {
      animation: lol 2000ms forwards;
      background-color: var(--secondary-color);
      transition: all 2000ms;
    }
  }
}

%customDateTimePicker {
  height: 31px;
  width: 168px;
  border: 1px solid var(--box-shadow);
  cursor: pointer;
  box-shadow: none;
  outline: none;
  color: var(--secondary-color);
  padding: 4px 11px 4px;
  transition: border 0.3s, box-shadow 0.3s;
  background-color: var(--primary-color);
  &:focus {
    border-color: consts.$color-blue-focus;
    outline: 0;
    box-shadow: 0 0 0 2px consts.$color-blue-focus-box-shadow;
  }
}

%inputWithTooltip {
  width: 100%;
  position: relative;
  div {
    right: 0px;
  }
}

%addToBlockInput {
  @extend %linkInput;
  width: 100%;
  position: relative;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  &::placeholder {
    color: var(--secondary-color);
  }

  &:focus {
    outline: none;
    margin: 0;
    box-sizing: border-box;
    box-shadow: 0 5px 10px -8px consts.$color-box-shadow-black-high-opacity;
  }
}

%addToBlockForm {
  display: flex;
  align-items: center;
  padding: 15px 0;
  box-sizing: border-box;

  button {
    @extend %customButton;
    min-width: 125px;
    min-height: 45px;
    font-size: inherit;
  }
}
