@use 'src/styles/consts';

.animation {
  opacity: 0.7;
  animation: skeleton-loading 1s linear infinite alternate;
  @keyframes skeleton-loading {
    0% {
      background-color: hsl(200, 20%, 70%);
    }

    100% {
      background-color: hsl(200, 20%, 95%);
    }
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 80%;
  margin-top: 45px;
  height: consts.$cardPreview-height;
  min-height: consts.$cardPreview-height;
  border-radius: 7px;

  &:nth-child(1) {
    margin-top: 0;
  }
}

.time {
  @extend .animation;
  border-radius: 7px;
  min-width: 105px;
  height: 60px;
}

.cardWrapper {
  display: flex;
  justify-content: flex-end;
  width: 60%;
  height: 95%;
  box-shadow: 0 1px 3px 0 var(--box-shadow);
  border-radius: 7px;

  &Reversed {
    flex-direction: row-reverse;
  }
}


.imageWrapper {
  @extend .animation;
  width: 40%;
  height: 100%;
  margin: 0;
  position: relative;
  bottom: 0;
  right: 0;
  opacity: 0.7;
  border-radius: 7px;
}

.cardInfo {
  height: 95%;
  margin: 0 18px;
  padding: 0;
  width: 60%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.card {
  @extend .animation;
  opacity: 0.7;
  border-radius: 7px;

  &Title {
    @extend .card;
    height: 18px;
    width: 40%;
  }

  &Agenda {
    @extend .card;
    width: 60%;
    height: 14px;
  }
}