@use 'src/styles/consts';

.editor {
  display: flex;
  flex-grow: 1;
  box-sizing: border-box;
  border: 1px solid consts.$color-shadowed-white;
  border-radius: 7px;
  margin: 5vh 0;
  position: relative;
  color: consts.$color-white;
  overflow: hidden;
  margin-top: 60px;

  &Lines {
    order: 2;
    width: 96px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 0;
    }
  }

  &LineGroupCommentIcon {
    position: absolute;
    right: 1%;
    bottom: 1px;
    z-index: consts.$z-index-s;
    cursor: pointer;
    color: consts.$color-white;
  }

  &Wrapper {
    position: absolute;
    width: 80%;
    height: 100%;
    left: 13%;

    section {
      width: 100% !important; //  need !important to overwrite the lib styles
    }

    @media (max-width: consts.$breakpoint-tablet) {
      width: 70%;
      left: 23%;
    }
  }

  &CommentTextarea {
    display: block;
    overflow: auto;
    position: absolute;
    top: 0;
    right: -5px;
    padding: 4px 4px 4px 6px;
    width: 100px;
    max-height: 200px;
    min-height: 48px;
    resize: vertical;
    color: consts.$color-black;
    outline: none;
    font-size: 14px;
    background-color: consts.$color-cloudgrey;

    &:focus {
      border-color: consts.$color-blue-focus;
      box-shadow: 0 0 0 0.2rem consts.$color-blue-focus-box-shadow;
    }
  }

  &LineGroup {
    position: relative;

    &Selected {
      border: 2px consts.$color-greenyellow solid;
    }

    &Icon {
      cursor: pointer;
      position: absolute;
      color: consts.$color-white;
      border: 1px solid consts.$color-icon-fill;
      border-radius: 7px;
      top: 50%;
      transform: translateY(-50%);
      z-index: consts.$z-index-s;
      height: 17px;
      width: 17px;
    }
  }

  &LineNumberField {
    height: 19px;
    min-width: 27px;
    position: relative;
    text-align: start;
    cursor: pointer;
    user-select: none;
    color: consts.$color-line-number;
    width: 100%;

    &Selected {
      background-color: consts.$color-blue-selection;
      color: consts.$color-white;
    }
  }

  &LineNumber {
    padding: 0 100px 0 30px;
  }

  &:nth-child(2) {
    .editorLines {
      @extend .editorLines;
      order: 1;
      right: 0;
    }

    .editorLineNumberField {
      text-align: end;
    }

    .editorLineGroupIcon {
      right: 7px;
    }

    .editorWrapper {
      @extend .editorWrapper;
      left: 7%;
    }

    .editorLineGroupCommentIcon {
      @extend .editorLineGroupCommentIcon;
      right: unset;
      left: 1%;
    }

    .editorLineGroupComment {
      @extend .editorLineGroupComment;
      left: 38px;
    }

    .editorCommentTextarea {
      @extend .editorCommentTextarea;
      left: -5px;
    }

    .editorLineNumber {
      padding: 0 30px 0 100px;
    }
  }
}

.svg {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  &Container {
    width: 100px;
  }
}

.B {
  margin-right: 30px;
}

.A {
  margin-left: 30px;
}

.prettierButton {
  position: absolute;
  bottom: 0;
  left: 0;
  background: none;
  border: none;
  color: darkgrey;
  cursor: pointer;
  z-index: 9999;
  &:hover {
    color: white;
    transition: color 0.3s ease, transform 0.2s ease;
  }
}

.fetchLoader {
  position: absolute;
  top: 280px;
  left: 740px;
  width: 40px;
}