.warningContainer {
  min-width: max-content;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container {
  @extend .warningContainer;
  min-width: unset;
  height: 100%;
  animation: pop-up 700ms linear;
  @keyframes pop-up {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}


.noDataImage {
  width: 67px;
  height: 67px;

  & * {
    fill: var(--secondary-color);
  }
}

