@use 'src/styles/consts';

.buttonIsActive {
  position: absolute;
  right: 10px;
  bottom: 10px;
  width: 35px;
  min-height: 35px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: var(--primary-color);
  border:3px solid var(--font-color);
  transition: all 400ms;
  user-select: none;
  cursor: pointer;
  opacity: 0.5;

  img{
    width: 15px;
    height: 15px;
    filter: var(--svg-icon-color);
  }

  &:hover{
    transition: 300ms;
    opacity: 1;
  }
}

