@use 'src/styles/consts';
@use 'src/styles/templates';

.initialCodeReview {
  width: 100%;
  font-size: consts.$font-size-l;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 3px;
  cursor: pointer;
  border-bottom: 1px solid consts.$color-shadowed-white;
}

.workspace {
  display: flex;
  background-color: consts.$color-black;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  margin: 5vh auto;
  position: relative;

  &SideBar {
    display: flex;
    flex-direction: column;
    color: white;
    flex-basis: 150px;
    text-wrap: normal;
    margin: 60px 30px 5vh 30px;
    border: 1px solid consts.$color-shadowed-white;
    border-radius: 7px;
  }
}

.line {
  position: fixed !important; //need !important to overwrite lib styles
  color: consts.$color-blue-focus;
  box-shadow: consts.$color-box-shadow 0 7px 29px 0;
}

.saveButton {
  @extend %customButton;
  @extend %saveExitButtons;
  left: 0;
  margin: 10px 10px;
}

.exitButton {
  @extend %customButton;
  @extend %saveExitButtons;
  right: 0;
  margin: 10px 10px;
}

.subCodeReviewItem {
  &Active {
    opacity: 1;
    background-color: consts.$color-shadowed-white;
    color: consts.$color-dark;

    &:hover {
      opacity: 1 !important;
    }
  }

  opacity: 0.5;
  transition: 300ms;
  font-size: consts.$font-size-m;
  padding: 3px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}

.subCodeReview {
  &Alert {
    margin: auto;
    text-align: center;
  }

  &Container {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow-y: auto;

    & ul {
      overflow-x: hidden;
    }

    & ul li {
      &:before {
        content: '-';
        padding-right: 3px;
      }

      white-space: nowrap;
      overflow: hidden;
      width: 151px;
      text-overflow: ellipsis;
      padding-left: 3px;
    }
  }
}

.provideSubCodeReviewButton {
  width: 100%;
  border-top: 1px solid consts.$color-white;
  padding: 3px;
  display: flex;
  justify-content: center;
  cursor: pointer;
  height: 50px;

  &Title {
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
    text-align: center;
    transition: 300ms;

    &:hover {
      opacity: 1;
    }
  }
}

.inputContainer {
  position: relative;
  width: 100%;

  div {
    right: 3px;
    top: 43px;
  }
}

.subCodeReviewTitleInput {
  @extend %addToBlockInput;
  color: consts.$color-shadowed-white;
  border-top: 1px solid consts.$color-shadowed-white;

  div {
    right: 0;
  }

  &::placeholder {
    color: consts.$color-shadowed-white;
  }
}

.loader {
  height: 100%;
}
