@use 'src/styles/consts';

.attribute {
    width: 27px;
    height: 27px;
    background-color: consts.$color-dark;
    border-radius: 50px;
    transition: ease all .5s;
    padding: 6px;
    display: flex;
    align-items: center;
    box-shadow: 0 1px 3px 0 consts.$color-box-shadow-black-medium-opacity;
    margin: 0 5px 0 5px;

    @media (max-width: consts.$breakpoint-tablet) {
      width: 25px;
      height: 25px;
      padding: 4px;
    }

    @media (max-width: consts.$breakpoint-tablet) {
      width: 20px;
      height: 20px;
    }
}
.attribute_animated {
  @extend .attribute;
     transform: scale(1.1) translateY(5%);
}
