@use 'src/styles/consts';

.card {
  &Item {
    min-width: max-content;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 80%;
    margin-top: 45px;
    height: consts.$cardPreview-height;
    min-height: consts.$cardPreview-height;
    transition: ease all 0.5s;
    animation: pop-up 400ms linear;
    @keyframes pop-up {
      0% {
        opacity: 0;
      }
      100% {
        opacity: 1;
      }
    }

    &:nth-child(1) {
      margin-top: 0;
    }

    &:nth-child(2n) .cardAttributes {
      left: 5%;
    }

    &:nth-child(2n) .cardWrapper {
      flex-direction: row-reverse;
    }

    &:nth-child(4) .cardImage,
    &:nth-child(4n + 6) .cardImage {
      border-radius: 0 7px 7px 0;
    }

    &:nth-child(1) .cardImage,
    &:nth-child(6n + 1) .cardImage {
      border-radius: 7px 0 0 7px;
    }

    @media (max-height: 270px) {
      min-height: 50px;
    }
  }

  &Date {
    font-size: consts.$font-size-m;
    min-width: 105px;
  }

  &Day {
    font-size: consts.$font-size-xl;
    color: var(--font-color);
  }

  &Time {
    font-size: consts.$font-size-m;
    color: var(--secondary-color);
    display: flex;
    justify-content: space-around;
  }

  &Day,
  &Time {
    text-align: center;
  }

  &Wrapper {
    height: 95%;
    width: 60%;
    display: flex;
    margin: 0 10px;
    justify-content: flex-end;
    border-radius: 7px;
    box-shadow: 0 1px 3px 0 var(--box-shadow);
    cursor: pointer;
    position: relative;

    @media (max-width: consts.$breakpoint-tablet) {
      min-width: 150px;
    }
  }

  &ImageWrapper {
    width: 40%;
    height: 100%;
    margin: 0;
    position: relative;
    bottom: 0;
    right: 0;
    border-radius: 7px;
    transition: ease all 1s;
  }

  &ImageWrapperLeftAnimated {
    @extend .cardImageWrapper;
    width: 42%;
    height: 112%;
    bottom: 12%;
    right: 20px;
  }

  &ImageWrapperRightAnimated {
    @extend .cardImageWrapperLeftAnimated;
    right: -20px;
  }

  &Image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0;
  }

  &RightSlash {
    @extend .cardImage;
    clip-path: polygon(21% 0, 100% 0, 100% 100%, 0% 100%);
    border-radius: 0 7px 7px 0;
  }

  &LeftSlash {
    @extend .cardImage;
    clip-path: polygon(0 0, 100% 0, 79% 100%, 0% 100%);
    border-radius: 7px 0 0 7px;
  }

  &RightCircle {
    @extend .cardImage;
    border-radius: 0 7px 7px 64%;
  }

  &LeftCircle {
    @extend .cardImage;
    border-radius: 7px 64% 0 7px;
  }

  &Info {
    height: 95%;
    margin: 0 18px;
    padding: 0;
    width: 60%;
    mix-blend-mode: hard-light;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  &Description {
    word-wrap: break-word;
    word-break: break-word;
    padding-left: 3px;
    resize: none;
    border: none;
    text-align: left;
  }

  &Title {
    @extend .cardDescription;
    font-size: consts.$font-size-m;
    font-weight: consts.$font-weight-bold;
    text-align: left;
    line-height: 28px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  &Agenda {
    @extend .cardDescription;
    max-height: calc((#{consts.$cardPreview-height} - #{consts.$cardTitle-height}));
    font-size: 14px;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    line-height: 1.5em;

    @media (max-height: consts.$breakpoint-mobile) {
      font-size: consts.$font-size-xs;
      line-height: 1.2em;
    }
    @media (min-width: consts.$breakpoint-wideScreen) {
      font-size: consts.$font-size-xl;
    }
  }

  &Videos {
    display: grid;
    justify-content: center;
    margin: 10px;
  }

  &Iframe {
    max-width: 250px;
    max-height: 150px;
    margin-bottom: 20px;
  }

  &Edit,
  &Delete,
  &Cancel,
  &View {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5%;
  }

  &Attributes {
    width: 130px;
    height: 30px;
    position: absolute;
    bottom: -25px;
    z-index: consts.$z-index-s;
    right: 10%;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;

    @media (max-width: consts.$breakpoint-tablet) {
      width: 90px;
      bottom: -30px;
    }
  }

  &TagContainer {
    overflow: hidden;
    white-space: nowrap;
    display: flex;
  }

  &TagItems {
    background-color: var(--box-shadow);
    border-radius: 10px;
    font-size: 85%;
    overflow: hidden;
    padding: 6px;
    margin: 2px;
    text-overflow: ellipsis;
    white-space: nowrap;
    box-sizing: border-box;
  }
}
